@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.maletin{margin:0.2em;padding:0.2em;border-radius:5px;text-align:center;position:relative;left:0;-webkit-transition:left 1s linear;transition:left 1s linear}.maletin span{line-height:0.5em;font-weight:bolder;font-family:Arial, Helvetica, sans-serif;font-size:20px;color:White;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.maletin img{width:50px;height:50px}.maletin:hover{background-color:#ffd700;cursor:pointer}@media screen and (max-width: 700px){.maletin{padding:0.1em}.maletin span{font-size:15px}}

.maletines div{display:flex;flex-wrap:wrap;text-align:center;justify-content:center}

.display-seleccionado{margin:0 auto;margin-top:2em;text-align:center;-webkit-transition:ease-in 1s;transition:ease-in 1s}.display-seleccionado span{font-family:Arial, Helvetica, sans-serif;font-style:oblique;font-weight:lighter;text-align:center;color:Grey}.seleccionado{background-color:gold;width:70px;height:70px;padding:1em;border-radius:50%;box-shadow:0px 10px 15px 3px rgba(0,0,0,0.5);margin:1em auto}.seleccionado span{color:white;font-style:unset}.seleccionado img{margin-top:0.4em}.seleccionado:hover{cursor:unset}

.tracker{background-color:grey;width:100%;height:2px;display:flex}.bar-completed{background-color:chartreuse;height:100%;max-width:100%}.segment{height:100%;flex-grow:1;position:relative}.bar{height:100%}.segment-completed{height:10px;width:10px;border-radius:50%;position:absolute;z-index:99;top:50%;right:0;-webkit-transform:translate(50%, -50%);transform:translate(50%, -50%);background-color:white;border:solid 2px;box-sizing:border-box}.segment-completed-filled{background-color:crimson;border:none}

.body{margin:0;padding:0;min-height:100vh;height:100%}.game{display:flex;justify-content:space-between;flex-direction:row;position:relative;height:100%}.title{color:gold;font-family:'Pacifico', Arial, Helvetica, sans-serif}@media screen and (max-width: 700px){.title{margin-top:0.5em;font-size:18px}}.left{width:100%;box-sizing:border-box;min-height:100vh;height:100%;padding:3em;padding-top:0.2em}@media screen and (max-width: 500px){.left{padding:1em}}.left h2{text-align:center;font-weight:normal;color:#4d5b66;font-size:30px}.left span{text-align:center}div.swal2-range{flex-wrap:wrap}.swal2-range input{display:block;width:100% !important}.swal2-range output{display:block;margin:0 auto;font-size:1.8em;white-space:nowrap;width:auto}.swal2-range output::before{content:'$ '}.footer-buttons{display:flex;flex-wrap:wrap;justify-content:center}.rigth{padding:0.6em;display:flex;flex-direction:column;width:200px;box-shadow:0px 10px 15px 3px rgba(0,0,0,0.5);box-sizing:border-box}@media screen and (max-width: 500px){.rigth{width:110px}}.valor{display:flex;justify-content:center;align-items:center;border-radius:5px;margin:0.2em;background-color:gold;padding:0.1em;text-align:center;font-size:0.7em;font-weight:bold;color:grey;flex-grow:1;-webkit-transition-property:opacity color background-color;transition-property:opacity color background-color;-webkit-transition:linear opacity 2s;transition:linear opacity 2s}.valor-abierto{opacity:0.2;color:White;background-color:Grey}.trato-aceptado,.bienvenida{text-align:center;text-align:center}.trato-aceptado h2,.bienvenida h2{margin-bottom:0}.trato-aceptado span,.bienvenida span{font-weight:lighter;text-align:center}.swal-confirmButton{background-color:Tomato}

